import React from 'react';
import { Link } from 'react-router-dom';
import '../src/Success.css';
import img1 from '../src/Images/main2.png';
import Flip from 'react-reveal/Flip';
import greentick from '../src/Images/greentick.png';

function Success() {
    


    return (
        <div className='successMessage' width='device-width' height='device-height'>
            <div className='Message'>
                <p><h6>Thank You!</h6></p>
            <img id='slidecaption' src={img1} height='120' width='150' />
                <p> <h2>Your order has been placed successfully<img src={greentick} height='30' width='30'/></h2></p>
                <div className='click-me'>
                <Link to='/' className='okaybtn' >OK</Link>
                </div>
            </div>
            
        </div>
    )
}

export default Success;
