import React from 'react'
import { Link } from 'react-router-dom';
import '../src/Footer.css';
import Home from './Home';

function Footer() {
    return (
        <div className='footerSection'>
              <div className='mainOne'>
                <div className='logo1'>
                   ASP CHEM
                </div>
                <div className='getstarted'>
                  <h3>Quick Links</h3>
                     <Link className='span' to='./Home'>Home</Link>
                     <Link to='./Reviews' className='span' >Reviews</Link>
                     <Link to='./Allproducts' className='span' >Products</Link>
                     
                </div>
                <div className='aboutsection'>
                  <Link to='./Contact' className='span' >Contact Us</Link>

                </div>
              </div>
              <footer>ASP CHEM © All rights reserved</footer>             
            </div>
    )
}

export default Footer;
