import React from 'react'
import '../src/Ourjourney.css';
import Fade from 'react-reveal/Fade';
import Flip from 'react-reveal/Flip';
import pinNine from '../src/Images/marker.png';

function Ourjourney() {
  return (
    <div className='Maincontainer'>
      <div className='titleJourney'>
       <Flip bottom> <p>OUR JOURNEY</p></Flip>
      </div>
      <div className='elements'>
        <div className='ONE'>
                 
                 <Fade left>
                 <div className='elementC'>
                 <span>2000</span>
                  <p>ASP CHEM was founded by Mr. Anjan Vasudeo Churi, Started dealing housekeeping items</p>
                 </div></Fade>       
                 <Flip top> <p><img src={pinNine} height='50' width='45'/></p></Flip>

        </div>
        <div className='TWO'>
                 <Fade left>
                 <div className='elementC'>
                 <span>2002</span>
                 <p>Started Manufacturing homecare liquid products in Boisar</p>
                 </div></Fade>
                 <Flip top> <p><img src={pinNine} height='50' width='45'/></p></Flip>

        </div>
        <div className='THREE'>
        <Fade left>
                 <div className='elementC'>
                 <span>2000</span>
                  <p>ASP CHEM started supplying premium waterparks</p>
                 </div></Fade> <Flip top> <p><img src={pinNine} height='50' width='45'/></p></Flip>
        </div>
        
        <div className='FOUR'>
                 
                 <Fade left>
                 <div className='elementC'>
                 <span>2000</span>
                 <p>All ASP CHEM products were now available at local shops</p>
                 </div></Fade><Flip top> <p><img src={pinNine} height='50' width='45'/></p></Flip>
        </div>
        <div className='FOURB'>
        <Fade left>  
        <div className='elementC'>
        <span>2000</span>
                 <p>85 local shops displayed ASP CHEM products</p>               
                   </div></Fade><Flip top> <p><img src={pinNine} height='50' width='45'/></p></Flip>
        </div>
        
        <div className='FIVE'>
        <Fade left>
        <div className='elementC'>
        <span>2000</span>
                 <p>ASP CHEM now started making tissue-paper and paper-cups</p>
                 </div></Fade><Flip top> <p><img src={pinNine} height='50' width='45'/></p></Flip>
        </div>
        <div className='SIX'>
                 
                 <Fade left>
                 <div className='elementC'>
                 <span>2000</span>
                 <p>Started dealing in pool cleaning products</p>
                 </div></Fade><Flip top> <p><img src={pinNine} height='50' width='45'/></p></Flip>
        </div>
        <div className='SEVEN'>
        <Flip top> <p><img src={pinNine} height='50' width='45'/></p></Flip>
                         <Fade right>
                 <div className='elementC'>
                   <span>2000</span>
                  <p>Introduced food packing items such as Aluminium containers, Aluminium foils, clean wrap and plastic containers</p></div></Fade>
        </div>
        <div className='EIGHT'>
        <Flip top> <p><img src={pinNine} height='50' width='45'/></p></Flip>
                <Fade right>
        <div className='elementC'>
        <span>2000</span>
                 <p>Started VINCENT Products, Same products made available in small packing suitable for customers</p>                 
                 </div></Fade>
        </div>
        <div className='NINE'>
        <Fade left>
        <div className='elementC'>
                   <span>TODAY</span>
                  <p>The 20 Years journey of the company had a lot of challenges, But the company conquered them.</p>
                 </div></Fade><Flip top> <p><img src={pinNine} height='50' width='45'/></p></Flip>
        </div>
        
        

      </div>
    </div>
  )
}

export default Ourjourney
