import React from 'react';
import img2 from '../src/Images/imgg2.png';
import greatescapewaterpark from '../src/Images/greatescapewaterpark.png';
import silenthillresort from '../src/Images/silenthillresort.png';
import hillzill from '../src/Images/hillzill.png';
import martininn from '../src/Images/martininn.jpg';
import nandanwan from '../src/Images/nandanwan.jpg';
import sarovar from '../src/Images/sarovar.png';
import  Fade from 'react-reveal/Fade';
import '../src/Ourcustomer.css';
import pga from '../src/Images/pga.png';
import pgb from '../src/Images/pgb.png';
import Slide from 'react-reveal/Slide';
import Footer from './Footer';

function Ourcustomer() {
    return (
        <div>
        <div className='our-customer'>
                <div className='section_one'>
               <Slide left> <img src={pga} height='100' width='50' className='imageone' /></Slide>
                <Slide right><img src={pgb} height='100' width='50' className='imagetwo'/></Slide>

                </div>
                <div className='section_two'>
               <Fade bottom> <p><h2>OUR CUSTOMERS</h2></p></Fade>
                   <p> Customer is our priority since last 15 years ASP CHEM is sustaining in the market and creating a
                        family bond with the customers. We have over 50+ hotels and resorts that show trust on ASP CHEM with each of their order.
                        Some of our premium clients for over more than 10+ years bonding are listed below:
                         </p>
                         <div className='partners'>
                             
                       <Fade left> <h5><img src={greatescapewaterpark}  width="100" height="100"/>The Great Escape Waterpark</h5></Fade>
                       <Fade right> <h5><img src={silenthillresort}  width="100" height="100"/>Silent Hills Resort</h5></Fade>
                       <Fade left>  <h5><img src={hillzill}  width="100" height="100"/>Hill Zill Resort</h5> </Fade>
                       <Fade right> <h5><img src={martininn}  width="100" height="100"/>Martin Inn Resort</h5></Fade>
                       <Fade left> <h5><img src={nandanwan}  width="100" height="100"/>Nandanwan Resort</h5></Fade>
                       <Fade right> <h5><img src={sarovar}  width="100" height="100"/>Sarovar Hotel</h5></Fade></div>
                        <span> <p> Apart from this hotels and resorts, The company is signing bonds with over 25 hotels for 5 years</p>
                         <p>ASP CHEM has successfully achieved trust of 2000+ individuals in last 15 years</p></span>
                </div>
                
            </div>
            <Footer /></div>
            
    )
}

export default Ourcustomer;
