import React, { useEffect } from 'react';
import './App.css';
import Header from './Header'
import Home from './Home';
import { BrowserRouter as Router, Switch, Route }
from "react-router-dom";
import Checkout from './Checkout';
import Login from './Login';
import { auth } from './firebase';
import { useStateValue } from './StateProvider';
import Payment from './Payment';
import Contact from './Contact';
import Allproducts from './Allproducts';
import Reviews from './Reviews';
import Ourvalues from './Ourvalues';
import Ourjourney from './Ourjourney';
import Ourcustomer from './Ourcustomer';
import naturesound from '../src/Images/naturesound.mp3';
import Success from './Success';

function App() {
  const [{}, dispatch] = useStateValue();

  useEffect(() => {

    auth.onAuthStateChanged(authUser => {
      console.log('THE USER IS >>>', authUser);

      if (authUser) {

        dispatch({
          type: 'SET_USER',
          user: authUser
        })


      } else {

        dispatch({
          type: 'SET_USER',
          user: null
        })

      }
    })
  }, [])


    return ( 
        <Router>
        <div className="App" width='device-width' height='device-height'>
        

            <Switch>
            <Route path="/login">
                <Login />
              </Route>
              <Route path="/checkout">
              <Header />
                <Checkout />
              </Route>
              <Route path="/payment">
                <Header />
                <Payment />
                
              </Route>
              <Route path="/Contact">
              <Header />
                <Contact />
              </Route>
              
              <Route path="/Allproducts">
              <Header />
                <Allproducts />
              </Route>

              <Route path="/Home">
              <Header />
                <Home />
              </Route>
              <Route path="/Reviews">
              <Header />
                <Reviews />
              </Route>
              
              
              <Route path="/Ourvalues">
              <Header />
                <Ourvalues />
              </Route>
              <Route path="/Reviews">
              <Header />
                
              </Route>
              <Route path="/Ourcustomer">
              <Header />
                <Ourcustomer />
              </Route>
              <Route path="/Ourjourney">
              <Header />
                <Ourjourney/>
              </Route>
              <Route path="/Success">
              <Header />
                <Success />
              </Route>
             
              <Route path="/">
              <Header />
                <Home />
              </Route>
              
              
              
              

            </Switch>
            <audio autoplay loop>
  <source src={naturesound} type="audio/mp3" />
</audio>

        </div>
        </Router>
    );
}

export default App;