import React from 'react';
import './Header.css';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import { Link } from 'react-router-dom';
import { useStateValue } from './StateProvider';
import { auth } from "./firebase";
import Dropdown from './Dropdown';

function Header() {
  const  [{ basket, user }, dispatch] = useStateValue();

  const handleAuthentication = () => {
     if (user) {
        auth.signOut();
     } 
  }

   return (
      <div className='header' >
         
         <Link className="header_logo" to= "./Home">
         <div class="btn-shine" target="_blank">ASP CHEM</div>
            
            </Link>
         
         <div className="header_search" >
            
              
            
             
              </div>
              <div className='right_head'>
              <Link className="company" to="./Company">
              <div class="dropdown">
                <span>Company▾</span>
              <div class="dropdown-content">
                  
               <Link className='ourvalues' to="./Ourvalues"  >
                   <h6>
                       Our Values
                   </h6>
               </Link>
               <Link className='our_customers' to="./Ourcustomer"  >
                   <h6>
                       Our Customer 
                   </h6>
               </Link>
               <Link to="./Ourjourney" className='ourjourneyi' >
                   <h6>
                     Our Journey
                   </h6>
               </Link>
               </div>
              </div>
               </Link>
              
              <div className='Reviews'>
              <Link className="reviews" to="./Reviews">
                 Reviews
               
                 
                 
            </Link></div>
              <div className='Products'>
              <Link className="product_list" to="/Allproducts">
                 <Dropdown />
               
                 
                 
            </Link></div>
           <div className='contact_company'>
              <Link className="contact_us" to="/Contact">
                 Contact
               
                 
                 
            </Link></div></div>

         <div className="header_nav" >
            
            <Link to="/checkout">
            <div className="header_optionBasket">
                 <ShoppingCartIcon />
                 <span className='header_optionLineTwo header_basketCount'>
                    {basket?.length}
                    </span>
            </div>
            </Link>
              
            </div>
            

         </div>
         

     
               );
};

export default Header;