import React from 'react';
import vision from '../src/Images/vision.png';
import mission from '../src/Images/mission.png';
import { Flip } from 'react-reveal/Flip';
import { Link } from 'react-router-dom';
import '../src/Ourvalues.css';
import { Fade } from 'react-reveal';
import globe from './Images/home.png';

function Ourvalues() {
    return (
        <div className='Containervalues'>
            <Fade top>
            <div className='heading_ourvalues'>
                <p><span>OUR VALUES</span></p>
            </div></Fade>
            <div className='globee'>
                <div className='imgtwo'><Fade right><img src={globe} height='200' width='200'/></Fade></div>

            </div>
            <div className='sections'>
                <Fade bottom>
                <div className='section_left'>
               
                   <p> <h2>OUR VISION</h2>
                  </p>
                   
                    <span><Fade left><h5>To provide our clients with the highest level of quality available in our industry and to always run our company in a honest and ethical manner.</h5></Fade></span>
                </div></Fade>
                <Fade bottom>              
                  <div className='section_right'>
                    <p><h2>OUR MISSION</h2></p>
                    <span><Fade right><h5> To be recognised as one of the leading companies providing consistent high quality products to our clients. </h5></Fade></span>
                </div></Fade>


            </div>
            
        </div>
 
    )
}

export default Ourvalues;