import React from 'react'
import { Link } from 'react-scroll'


function Dropdown() {
    return (
        <div class="dropdown">
  <span>Products▾</span>
  <div class="dropdown-content">
  <Link to="others" smooth={true} className='Otherstwo' duration={500} >
                   <h6>
                      Others 
                   </h6>
               </Link>
               <Link to="covid-essentials" className='Covidessentialstwo' smooth={true} duration={500} >
                   <h6>
                       Covid-Essentials 
                   </h6>
               </Link>
               <Link to="daily-home-essentials" className='Dailyhomeessentialstwo' smooth={true} duration={500} >
                   <h6>
                       Daily Home Essentials 
                   </h6>
               </Link>
               <Link to="hotel-essentials" className='Hotelessentialstwo' smooth={true} duration={500} >
                   <h6>
                      Hotel Essentials 
                   </h6>
               </Link>
               <Link to="personal-care" className='Personalcaretwo' smooth={true} duration={500} >
                   <h6>
                    Personal Care 
                   </h6>
               </Link>
               <Link to="swimming-pool-essentials" className='Swimmingpoolessentialstwo' smooth={true} duration={500} >
                   <h6>
                      Swimming Pool Essentials 
                   </h6>
               </Link>
  
  </div>
</div>
    )
}

export default Dropdown
