import React from 'react'
import '../src/Reviews.css';
import rev7 from '../src/Images/r7.PNG';
import rev2 from '../src/Images/r2.PNG';
import rev15 from '../src/Images/r15.PNG';
import rev4 from '../src/Images/s8.PNG';
import rev12 from '../src/Images/r12.PNG';
import rev16 from '../src/Images/r16.PNG';
import Footer from './Footer';

function Reviews() {
    return (
        
<div>

<div className='reviews_container'>
                    <div class= 'container_pp'>
                   <div class="container_content_u">
                      <div class="container_content_inner">
                      <div class="title">
                        <h1>Customer Reviews</h1>
                            </div>
                         <div class="par">
                               <p>
                             An Experience you'll love to encounter again and again❤️ 
                               </p>
                                   </div>
                          <div class="btns">
                      <button class='btns_more' > See more </button>
                         </div>
                                         </div>
                              </div>
                              <div class="container_outer_img">
                                 <div class="img-inner">
                                   
                                 <div class="cube-wrap">
                                   <div class="cube anim fadeInUp one">
                                              <img src={rev2} />
                                </div>

                         <div class="cube anim fadeInDown two">
                         <img src={rev15} />
                                      </div>

                              <div class="cube anim fadeInLeft three">
                              <img src={rev4} />
                        </div>
                        <div class="cube anim fadeInDown two">
                        <img src={rev12} />
                                      </div>
  
                          <div class="cube anim fadeInRight four">
                          <img src={rev7} />
                   </div>
                   <div class="cube anim fadeInUp one">
                                              <img src={rev16} />
                                </div>
                     </div>
                            
                          </div>
                       </div>
                     
                  </div>
                  </div><Footer /></div>)
  

}

export default Reviews;