import React from 'react';
import "./Home.css";
import all from '../src/Images/products/all.jpg';
import img1 from '../src/Images/main2.png';
import Fade from 'react-reveal/Fade';
import TopContainer from './About/TopContainer';
import image2 from '../src/Images/nature.jpg';
import { Parallax } from 'react-parallax';
import TypingEffect from './TypingEffect';
import image4 from '../src/Images/abstract.jpg';
import Footer from './Footer';



function Home() {

    const styles = {
        fontFamily: "sans-serif",
        textAlign: "center"
      };
      const insideStyles = {
        
        transform: "translate(0%,0%)",
        justifycontent: 'center',
        height: '100vh'
        
        
      };
    


    return (
        <div className='home'>
            
            <TopContainer /> 
            <Parallax bgImage={image2} strength={-150}>
      <div style={{ height: 800 }}>
        <div style={insideStyles}>
        
            <div className='home_containertwo'>
                <div className='left'>
                    <Fade left>
                   <div className='aspchem'>
                       <p>ABOUT US</p>
                   </div></Fade>
                   <div className='logoimage'>
                   <img src={img1} height='200' width='260' />
                   </div>
                </div>
                <Fade right>
                <div className='right'>
                  <TypingEffect/>
                </div></Fade>
            </div>
        

        </div>
      </div>
    </Parallax>

    <Parallax bgImage={image4} strength={500}>
      <div style={{ height: 700 }}>
        <div style={insideStyles}>
        <div className="home_container">
                <Fade bottom>
                <div className='vincenttitle'>
            <h2>VINCENT</h2></div></Fade>
            <div className='kit'>
                          <img src={all} height='250' width='250'/>

            </div>
            <p>VINCENT is a sub-brand of ASP CHEM. Vincent is a Latin word which means Conquering. In such hard time where covid is all over the globe,
		 we aim at conquering the floor, hands, utensils,etc with cleanliness and hygine. There are 4 main VINCENT products which includes VINCENT WHITE PHENYL for a clean and hygenic floor.
		 To kill germs and deadly bacteria on your hands we recomming you to use VINCENT HANDWASH which is made with special chemicals and fragnance to give you a soothing experience.
		 Sticky oily utensils attracts germs, bacteria and micro-organisms which are harmful for utensils and well as our body, VINCENT DISHWASH gives you a guarantee of cleaning of germs and bacteria 
		 from your utensils. A 30ml pocket friendly VINCENT HAND SANITIZER which is a covid essential is made just for you with special aloe vera extract which helps cleaning the germs and is absolutely safe for any type of skin. The company deliveres the products all over India.</p>
            
            
            

            
            
            
            </div>
            
        </div>
      </div>
    </Parallax>
    <Footer />   
            
            
            
        </div>
    )
}

export default Home;
