import React from 'react';
import TopContent from '../About/TopContent';
import '../About/TopContainer.css';
import AliceCarousel from 'react-alice-carousel';
import "react-alice-carousel/lib/alice-carousel.css";
import image1 from '../Images/products/wallone.jpg'
import image2 from '../Images/products/walltwo.jpg'
import image3 from '../Images/products/wallthree.jpg'
import image4 from '../Images/products/wallfour.jpg'

function TopContainer() {
    return (
        <div name='about' className='topContainer'>
            <AliceCarousel autoPlay infinite autoPlayInterval="100000">
      <div><img src={image1} className="sliderimg" alt=""/><TopContent /></div>
      <img src={image2} className="sliderimg" alt=""/>
      <img src={image3} className="sliderimg" alt=""/>
      <img src={image4} className="sliderimg" alt=""/>
    </AliceCarousel>
    
            
            

        </div>
    )
}

export default TopContainer;





