import React from 'react';
import './Payment.css';
import { useStateValue } from "./StateProvider";
import CheckoutProduct from './CheckoutProduct';
import { Link } from 'react-router-dom';
import { useState } from 'react';
import { getBasketTotal } from './reducer';
import CurrencyFormat from "react-currency-format";
import { Button } from '@material-ui/core';
import { db } from './firebase';
import Fade from 'react-reveal/Fade'; 



function Payment() {
    const [{ basket, user }, dispatch, setbasket] = useStateValue();

    const [fullname, setfullname] = useState("");
    const [mobile, setmobile] = useState("");
    const [email, setemail] = useState("");
    const [address, setaddress] = useState("");
    const [streetaddress, setstreetaddress] = useState("");
    const [landmark, setlandmark] = useState("");
    const [city, setcity] = useState("");
    const [state, setstate] = useState("");
    const [pincode, setpincode] = useState("");

    const [buttonSuccess, setButtonSuccess] = useState(false);

    
    

    const handleSubmit = (e) => {
        e.preventDefault();

        db.collection('orders').add({

          basket: basket,
          fullname: fullname,
          email: email,
          mobile: mobile,
          address: address,
          streetaddress: streetaddress,
          landmark: landmark,
          city: city,
          state: state,
          pincode: pincode,
          basket: basket,
          



          
        
        })
        console.log("Geeks");
setTimeout(() => document.location.href='/Success', 1000);


        
        
        

        setfullname('');
        setemail('');
        setmobile('');
        setaddress('');
        setstreetaddress('');
        setlandmark('');
        setcity('');
        setstate('');
        setpincode('');


        
        
        
    };

     return (
        
         

            <div className='payment_container'>
                <h2>
                    Checkout (<Link to="/checkout">{basket?.length} items </Link>)
                </h2>
                <div className='payment_section'>
                    <div className='payment_title'>
                        <h4>Address</h4>
                    </div>
                    <div className='payment_address'>
                        <p>{user?.email}</p>
                        <div className="col-md-8 col-12 mx-auto">
                        <form onSubmit={handleSubmit}>

                            <div class="mb-3">
                                <label for="exampleFormControlInput1" class="form-label"></label>
                                <input type="text" class="form-control" id="exampleFormControlInput1" name="fullname" value={fullname}  onChange={(e) => setfullname(e.target.value)} placeholder="Name" />
                            </div>
                            <div class="mb-3">
                                <label for="exampleFormControlInput1" class="form-label"></label>
                                <input type="number" class="form-control" id="exampleFormControlInput1" name="phone" placeholder="Mobile number" value={mobile} onChange={(e) => setmobile(e.target.value)}/>
                            </div>
                            <div class="mb-3">
                                <label for="exampleFormControlInput1" class="form-label"></label>
                                <input type="email" class="form-control" id="exampleFormControlInput1" name="email"  value={email}  onChange={(e) => setemail(e.target.value)} placeholder="Email" />
                            </div>
                            <div class="mb-3">
                                <label for="exampleFormControlInput1" class="form-label"></label>
                                <input class="form-control" id="exampleFormControlTextInput1"  name="address"  value={address}  onChange={(e) => setaddress(e.target.value)} placeholder="address"></input>

                            </div>
                            <div class="mb-3">
                                <label for="exampleFormControlInput1" class="form-label"></label>
                                <input class="form-control" id="exampleFormControlTextInput1"  name="street address"  value={streetaddress}  onChange={(e) => setstreetaddress(e.target.value)} placeholder="street address"></input>

                            </div>
                            <div class="mb-3">
                                <label for="exampleFormControlInput1" class="form-label"></label>
                                <input class="form-control" id="exampleFormControlTextInput1"  name="landmark"  value={landmark}  onChange={(e) => setlandmark(e.target.value)} placeholder="landmark"></input>

                            </div>
                            <div class="mb-3">
                                <label for="exampleFormControlInput1" class="form-label"></label>
                                <input class="form-control" id="exampleFormControlTextInput1"  name="city"  value={city}  onChange={(e) => setcity(e.target.value)} placeholder="city"></input>

                            </div>
                            <div class="mb-3">
                                <label for="exampleFormControlInput1" class="form-label"></label>
                                <input class="form-control" id="exampleFormControlTextInput1"  name="state"  value={state}  onChange={(e) => setstate(e.target.value)} placeholder="state"></input>

                            </div>
                            <div class="mb-3">
                                <label for="exampleFormControlInput1" class="form-label"></label>
                                <input class="form-control" id="exampleFormControlTextInput1"  name="pin-code"  value={pincode}  onChange={(e) => setpincode(e.target.value)} placeholder="pin-code"></input>

                            </div>
                            <div class="col-12">
                            
                                
                                          <Link type="Submit" className='order-btn' onClick={handleSubmit} to='/Home'  >
                                              Order Now  
                                        </Link>
                            </div>
                        </form>
                    </div>

                    </div>

                </div>
                <div className='payment_section'>
                <div className='payment_title'>
                    <h4>Items:</h4>
                </div>
                <Fade right>
                <div className='payment_items' >
                    {basket.map(item => (
                       <CheckoutProduct
                           id={item.id}
                           title={item.title}
                           image={item.image}
                           price={item.price}
                           rating={item.rating}
   
                     />
                     )) }
                </div></Fade>
                
                
                </div>
               
                    <div className='payment_priceContainer'>
            <CurrencyFormat
              renderText={(value) => (
                  
                  <p>
                      Subtotal ({basket.length} items):  
                      <strong>{value}</strong>
                  </p>
                  
              )}
              decimalScale={2}
              value={getBasketTotal(basket)}
              displayType={"text"}
              thousandSeparator={true}
              prefix={"Rs."}
              />
              
              </div>
             
              </div>
              
       
    )
}

export default Payment;
