import React, { useState } from 'react'
import "./Product.css";
import { useStateValue } from './StateProvider';
import ImageGallery from 'react-image-gallery';
import { Link } from 'react-router-dom';
import Popup from './Popup';
import Fade from 'react-reveal/Fade';



function Product({ id, title, image, price, rating, description, usage, storage, safety, descriptiontitle, usagetitle, storagetitle, 
  safetytitle, dpone, dptwo, dpthree, dpfour, dpfive, img1, img2, img3, uione, uitwo, uithree, uifour, uifive, stione, stitwo, stithree, stifour, stifive , sione, sitwo, sithree, sifour, sifive  }) {
      const [{ basket }, dispatch] = useStateValue();
      
      const [isOpen, setIsOpen] = useState(false);
    
      const addToBasket = () => {
          //dispatch the item into datalayer
          dispatch({
              type: 'ADD_TO_BASKET',
              item: {
                  id: id,
                  title: title,
                  image: image,
                  price: price,
                  rating: rating,
                  
              },
          });
      };

      
      
      
    
      return (
          
              

    <div className='card'>
            <div class="container page-wrapper">
  <div class="page-inner">
    <div class="row">
     
      <div class="el-wrapper" >
        <div class="box-up" onClick={() => setIsOpen(true)} >
          <img class="img" src={image} alt="product" />
          <div class="img-info">
            <div class="info-inner">
            <span id='btn-popup' class="popup-btn"  >
              <span class="p-name">{title}</span></span>
              
              <span class="p-company">{Array(rating).fill().map((_, i) => 
                    (
                        <>★</>
                    ))}</span>
            </div>
            
          </div>
        </div>

        <div class="box-down">
          <div class="h-bg">
            <div class="h-bg-inner"></div>
          </div>

          <a class="cart" href="#">
            <span class="price">Rs.{price}</span>
            <span class="add-to-cart">
              <span class="txt" onClick={addToBasket}>Add in cart</span>
            </span>
          </a>
                </div>
              </div>
            </div>
          </div>
        </div>
        <Popup open={isOpen} onClose={() => setIsOpen(false)}>
        <div className='popup-view'>
          <div className='popup-card'>
            <div className='product-img'>
            <div class="slider2">
      <span id="slide-1"></span>
      <span id="slide-2"></span>
      <span id="slide-3"></span>
      <div class="image-container">
        <img src={image} class="slide2" width="500" height="100" />
        <img src={img2} class="slide2" width="500" height="100" />
        <img src={img3} class="slide2" width="500" height="100" />
      </div>
      <div class="buttons">
        <a href="#slide-1"></a>
        <a href="#slide-2"></a>
        <a href="#slide-3"></a>
      </div>
      
    </div>
    <br/>
    <span className='price'>Rs.{price}</span>
	       </div>
            <div className='info'>
               <Fade right><p className='titlee'>{title}</p></Fade>Fade<hr/> <Fade bottom><span>{descriptiontitle}</span></Fade> <br/>{dpone}<br/> {dptwo}<br/> {dpthree}<br/> {dpfour}<br/> {dpfive}<hr/><Fade left><span> {usagetitle}</span></Fade><br/>{uione}<br/>{uitwo}<br/> {uithree}<br/> {uifour}<br/> {uifive}<hr/><Fade right> <span>{storagetitle}</span></Fade><br/>{stione}<br/>{stitwo}{stithree}{stifour}{stifive}<hr/> 
 <Fade left><span> {safetytitle}</span></Fade> <br/>  {sione}<br/> {sitwo} {sithree} {sifour}{sifive}
            
            <hr/>
            <br/>
            <a class="add-cart-btn">
              
              <span class="txt" onClick={addToBasket}>Add in cart</span>
            </a>
               </div>
          </div>
 </div>
</Popup>
</div>

            
        

          
        
    )
}
 
export default Product;
