import React, { Component } from "react";
import PropTypes from "prop-types";
import styles from "../src/TypingEffect.css";

class TypingEffect extends Component {
  static propTypes = {
    text: PropTypes.string.isRequired,
    wordsPerSecond: PropTypes.number.isRequired
  };

  static defaultProps = {
    text:
      "ASP CHEM is a Indian Manufacturer and Dealer of Homecare products. We make use of chemical and chemistry in such a way that it creates a sustainable Future.The Company is located on the western coast of Arabian sea and has near acquaintances with MAHARASHTRA INDUSTRIAL DEVELOPEMENT CORPORATION(MIDC) in Boisar and GUJARAT INDUSTRIAL DEVELOPEMENT CORPORATION(GIDC) in Vapi. We manufacture Homecare products, which is used in sectors such as hospitals, schools, personal care, hotel industry , etc.",
    wordsPerSecond: 50
  };

  state = {
    runningText: "",
    index: 0
  };

  componentDidMount() {
    this.generateText();
  }

  componentDidUpdate() {
    this.generateText();
  }

  generateText = () => {
    //Clear existing time if running
    clearTimeout(this.timer);

    const { runningText, index } = this.state;
    const { text, wordsPerSecond } = this.props;

    //Speed of generting text
    const speed = 900 / wordsPerSecond;

    if (index < text.length) {
      this.timer = setTimeout(() => {
        this.setState({
          runningText: runningText + text[index],
          index: index + 1
        });
      }, speed);
    } else {
      //Clear timer if generated completely
      clearTimeout(this.timer);
    }
  };

  componentWillUnmount() {
    clearTimeout(this.timer);
  }

  render() {
    const { runningText } = this.state;

    return (
      <div className={styles.typingArea}>
        {runningText}
        <span className={styles.blinkingCursor}></span>
      </div>
    );
  }
}

export default TypingEffect;